@import url("https://fonts.googleapis.com/css?family=Barlow+Condensed");

body {
  background-image: url("img/bg.png ");
  background-position-x: center;
  margin: 0;
  padding: 0;
  font-family: "Barlow Condensed", sans-serif;
}

.homepage-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.home-container {
  background-color: #f3f3f3;
  border: 3px #e7d1ab solid;
  border-radius: 33px;
  width: 80%;
  padding: 50px 0;
}

.small-avatar {
  border-radius: 115px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.home-name {
  text-align: center;
  font-size: 42px;
  color: #707070;
  margin: 0;
}

.home-subtitle {
  text-align: center;
  font-size: 17px;
  font-weight: lighter;
  color: #707070;
  margin-bottom: 40px;
}

.home-navigation {
  text-align: center;
}

.home-left-btn {
  color: #707070;
  font-size: 17px;
  background-color: #ffffff;
  border: 1px #707070 solid;
  border-top-left-radius: 32px;
  border-bottom-left-radius: 32px;
  padding: 11px 36px;
  text-decoration: none;
  transition: 0.5s;
  display: inline-block;
}

.home-left-btn:hover {
  background-color: #fffdf4;
}

.home-left-btn::before {
  content: "<<";
  margin-left: -35px;
  opacity: 0;
  transition: 0.5s;
  position: absolute;
}

.home-left-btn:hover:before {
  opacity: 1;
  margin-left: -25px;
}

.home-right-btn {
  color: #707070;
  font-size: 17px;
  display: inline-block;
  background-color: #ffffff;
  border: 1px #707070 solid;
  border-top-right-radius: 32px;
  border-bottom-right-radius: 32px;
  padding: 11px 40px;
  text-decoration: none;
  transition: 0.5s;
}

.home-right-btn::after {
  content: ">>";
  margin-left: 25px;
  opacity: 0;
  transition: 0.5s;
  position: absolute;
}

.home-right-btn:hover:after {
  opacity: 1;
  margin-left: 15px;
}

.home-right-btn:hover {
  background-color: #fffdf4;
}

.top-navigation {
  height: 80px;
  display: flex;
}

.top-navigation-left {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #dddddd;
  width: 50%;
}

.top-navigation-btn-left {
  font-size: 30px;
  color: #000;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  border: 3px #ffffff solid;
  padding: 3px 0;
  width: 85%;
  border-bottom-left-radius: 44px;
  border-top-right-radius: 18px;
  text-transform: capitalize;
}

.top-navigation-right {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #dddddd;
  width: 50%;
}

.top-navigation-btn-right {
  font-size: 30px;
  color: #000;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  border: 3px #ffffff solid;
  padding: 3px 0;
  width: 85%;
  border-bottom-right-radius: 44px;
  border-top-left-radius: 18px;
  text-transform: capitalize;
}

.about-wrap {
  background-color: #fff;
  min-height: 100vh;
}

.portfolio-wrap{
  background-color: #fff;
  min-height: 100vh;
}

.page-title {
  font-size: 70px;
  font-weight: lighter;
  color: #000;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 10;
}

.medium-avatar-image {
  width: 80vw;
}

.about-image-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-section-two {
  display: none;
  height: 100vh;
  flex: 50%;
  background-image: url("img/medium-avatar.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.about-summary-wrap {
  margin: 0 10px;
}
.about-summary {
  font-size: 20px;
  color: #000;
}

.about-form {
  width: 85%;
  margin: 50px 10px;
}

.about-form label {
  font-size: 20px;
  color: #000;
  margin-bottom: 5px;
}

.about-form::placeholder {
  font-size: 18px;
  color: #949393;
}

.about-input {
  border: 2px #e7d1ab solid;
  width: 100%;
  padding: 10px;
  margin: 10px 0;
}

.about-page-btn {
  float: right;
  margin-right: -25px;
  padding: 10px 40px;
  background: transparent;
  color: #000;
  font-size: 20px;
  border: 2px solid #e7d1ab;
}

.clearfix {
  clear: both;
}

.about-section-2{
  display:none;
}

@media (min-width: 576px) {
  .home-container {
    width: 65%;
    padding: 70px 0;
  }

  .about-form {
    width: 90%;
  }
}

@media (min-width: 768px) {
  .home-container {
    width: 50%;
  }

  .medium-avatar-image {
    width: 60vw;
  }

  .about-form {
    width: 92%;
  }
}

@media (min-width: 992px) {
  .home-container {
    width: 40%;
  }
}

@media (min-width: 1200px) {
  .home-container {
    width: 30%;
  }

  .top-navigation {
    height: 200px;
    display: inherit;
  }

  .top-navigation-left {
    width: 30%;
    float: left;
    -webkit-clip-path: polygon(100% 0, 0 0, 0 100%);
    clip-path: polygon(100% 0, 0 0, 0 100%);
    padding-bottom: 100px;
    padding-top: 50px;
    display: inherit;
  }

  .top-navigation-right {
    width: 30%;
    float: right;
    -webkit-clip-path: polygon(100% 0, 75% 0, 100% 25%);
    clip-path: polygon(100% 0, 0 0, 100% 100%);
    padding-bottom: 100px;
    padding-top: 50px;
    display: inherit;
  }

  .top-navigation-btn-left {
    width: 40%;
    padding: 5px;
    margin-left: 15px;
    float: left;
  }

  .top-navigation-btn-right {
    width: 40%;
    padding: 5px 0;
    float: right;
    margin-right: 15px;
  }

  .page-title {
    margin-top: -100px;
  }

  .medium-avatar-image {
    display: none;
  }

  .about-inner-wrap {
    display: flex;
  }

  .about-section-1{
    margin-left:30px;
  }

  .about-section-2{
    display:initial;
    background-image: url('img/medium-avatar.png');
    background-size: cover;
    background-repeat: no-repeat;
    height:100vh;
    width:70vw;
  }
}

@media (min-width: 1700px) {
  .home-container {
    width: 20%;
  }
}
